.logIn-back{
    background-image: linear-gradient(to right bottom, #494846b4, #3a3a38e0), url(/src/Pages/bg.jpg);
    background-size: cover;
    height: 100vh;
    
  }
  
  
  .logIn-container{
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      margin-top: 0rem;
      background-color: rgba(29, 57, 85, 0.267);
  }
  
  
  .log-inBox{
    width: 20%;
    height: 35rem;
    margin-top: 5rem;
    display: grid;
    border-radius: 10px;
    box-shadow: 0 0 2px rgba(15, 15, 15, 0.28);
    background-image: linear-gradient(to right bottom, #f0eee76e, #faf9f2d5);
  
  }
  
  
  .top-container{
  width: 100%;
  height: 10rem;
  display: flex;
  margin-top: 2rem;
  flex-direction: column;
  padding: 0 0rem;
  font-family: 'Hammersmith One';  
  color: rgb(80, 74, 74);
  border-radius: 3%;
  
  }
  
  .top-container h3{
      display: block;
      text-align: right;
      font-size: 1.5rem;
      padding: 1rem 2.5rem;
      font-weight: 700;
      margin-bottom: 0.1rem; 
      
  }
  
  .top-container h4{
      display: block;
      text-align: right;
      font-size: 1.2rem;
      padding: 0rem 3.5rem;
      font-weight: 600;
  }
  
  
  .navbars {
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      z-index: 1;
      height: 4rem;
      
      
    }
    
    .menu {
      max-width: 72rem;
      width: 100%;
      margin: 0 auto;
      padding:  0 2rem;
      display: flex;
      justify-content: space-between;
      align-items: center;
      color: #fff;
      
    }
    
    .logo {
      font-size: 1.1rem;
      font-weight: 600;
      text-transform: uppercase;
      letter-spacing: 2px;
      line-height: 4rem;
    }
  
  .Sign-input{
      width: 80%;
      height: 15rem;
      margin-bottom: 2rem;
      display: grid;
      justify-self: center;
      align-items: center;
      padding-bottom: 0rem;
      border-radius: 10px;
      margin-left: -0.5rem;
      font-family: 'Hammersmith One';  
      background-color: rgba(231, 225, 225, 0.363);
      border: solid rgb(219, 216, 216);
      box-shadow: 0 0 2px rgba(15, 15, 15, 0.28);
      
  }

 

  
  
  .log-placeholder{
      width: 70%;
      height: 3rem;
      border:none;
      border-radius: 5px;
      font-size: 1.1rem;
      font-weight: 550;
      margin-bottom: 1rem;
      font-family: 'Hammersmith One';  
  }
  .log-placeholder:hover{
      border-bottom: solid gray;
  }
  
  .log-content{
      display:block;
      margin-top: 1rem;
      text-align: center;
      margin-bottom: 0rem;
  }
  
  .log-inBox h3{
  font-size: 1.5rem;
  text-align: center; 
  color:gray; 
  text-decoration: none; 
  
  }
  
  
  .log-submit{
      display: grid;
      align-items: center;    
  }
  
  
  
  .log-btn{
      color:#fff;
      text-decoration: none;
      text-transform: uppercase;
      background-color: rgb(143, 142, 142);
      font-size: 1rem;
      margin-left: 2.5rem;
      padding: 0.8rem 1rem;
      border-radius: 15px;
      width: 70%;
      text-align: center;
      transition: all 0.2s;
      font-family: 'Hammersmith One';    
  }
  
  .log-btn:hover{
      background-color: rgb(61, 59, 59);
      color: #fff;
  }
  
  .log-input a{
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 1.5rem;
      text-align: center; 
      color:gray; 
      text-decoration: none;
  }
  
  .sign-btn{
   margin-top: 2rem;
  }
  
  
  .sign-btn1 {
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 1rem;
      text-align: center; 
      color:rgb(96, 96, 97); 
      text-decoration: none;
      font-family: 'Hammersmith One';  
      
  }
  
  
  .register-btn{
      
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 1rem;
      text-align: center; 
      color:rgb(58, 58, 119); 
      text-decoration: none;
      font-family: 'Hammersmith One';  
      cursor: pointer;
      
  }


